import React from 'react';
import PropTypes from 'prop-types';
import { Link, graphql, useStaticQuery } from 'gatsby';
import Url from 'url';

import { Layout, HeaderPost, AuthorList, ImgSharp } from '../components/common';
import { StickyNavContainer } from '../components/common/effects';
import { ArticleMeta } from '../components/common/meta';

import { PostClass } from '../components/common/helpers';

/**
 * Single post view (/:slug)
 *
 * This file renders a single post and loads all the content.
 *
 */
const Post = ({ post, location, children }) => {
  const {
    site: {
      siteMetadata: { siteUrl },
    },
  } = useStaticQuery(graphql`
    query SiteMetaDataQueryPost {
      site {
        siteMetadata {
          siteUrl
        }
      }
    }
  `);

  const canonical = Url.resolve(siteUrl, location.pathname);

  const featImg = post.feature_image;
  const fluidFeatureImg =
    post.featureImageSharp &&
    post.featureImageSharp.childImageSharp &&
    post.featureImageSharp.childImageSharp.fluid;

  const postClass = PostClass({
    tags: post.tags,
    isFeatured: featImg,
    isImage: featImg && true,
  });

  const author = {};

  return (
    <>
      <ArticleMeta canonical={canonical} post={post} author={author} />
      <StickyNavContainer
        isPost
        activeClass="nav-post-title-active"
        render={sticky => (
          <Layout
            isPost
            sticky={sticky}
            header={<HeaderPost sticky={sticky} title={post.title} />}
          >
            <div className="inner">
              <article className={`post-full ${postClass}`}>
                <header className="post-full-header">
                  {post.primary_tag && (
                    <section className="post-full-tags">
                      <Link to={`/kcdc-covid-19-response-guidelines/`}>
                        {post.primary_tag.name}
                      </Link>
                    </section>
                  )}

                  <h1
                    ref={sticky && sticky.anchorRef}
                    className="post-full-title"
                  >
                    {post.title}
                  </h1>

                  {post.custom_excerpt && (
                    <p className="post-full-custom-excerpt">
                      {post.custom_excerpt}
                    </p>
                  )}

                  <div className="post-full-byline">
                    <section className="post-full-byline-content">
                      <AuthorList authors={post.authors} isPost />

                      <section className="post-full-byline-meta">
                        <h4 className="author-name">
                          {post.authors.map((author, i) => (
                            <Link key={i} to={`/about`}>
                              {author.name}
                            </Link>
                          ))}
                        </h4>
                        <div className="byline-meta-content">
                          <time
                            className="byline-meta-date"
                            dateTime={post.published_at}
                          >
                            {post.published_at_pretty}&nbsp;
                          </time>
                        </div>
                      </section>
                    </section>
                  </div>
                </header>

                <figure className="post-full-image">
                  <ImgSharp
                    fluidClass="kg-card kg-code-card"
                    fluidImg={fluidFeatureImg}
                    srcImg={featImg}
                    title={post.title}
                  />
                </figure>

                <section className="post-full-content">
                  <div className="post-content load-external-scripts">
                    {children}
                  </div>
                </section>
              </article>
            </div>
          </Layout>
        )}
      />
    </>
  );
};

Post.propTypes = {
  post: PropTypes.shape({
    codeinjection_styles: PropTypes.string,
    id: PropTypes.string.isRequired,
    title: PropTypes.string.isRequired,
    html: PropTypes.string.isRequired,
    custom_excerpt: PropTypes.string,
    feature_image: PropTypes.string,
    featured: PropTypes.bool,
    tags: PropTypes.arrayOf(PropTypes.object.isRequired),
    authors: PropTypes.arrayOf(PropTypes.object.isRequired).isRequired,
    primary_tag: PropTypes.shape({
      name: PropTypes.string,
      slug: PropTypes.string,
    }),
    published_at: PropTypes.string.isRequired,
    published_at_pretty: PropTypes.string.isRequired,
    children: PropTypes.arrayOf(PropTypes.object),
    featureImageSharp: PropTypes.object,
  }).isRequired,
  location: PropTypes.object.isRequired,
  children: PropTypes.node.isRequired,
};

export default Post;
